import axios from 'axios';
import * as constants from './constants';
import { Modal, message } from 'antd';

export const getGroupList = () => {
  return (dispatch) => {
    axios.get('/gonghaoTag')
      .then(res => {
        if (res.data.success) {
          let gonghaoTagList = res.data.data;
          const action = {
            type: constants.GET_GONGHAO_TAG_LIST,
            gonghaoTagList
          };
          dispatch(action)
        }
      })
  }
};

export const getAllGonghaoList = () => {
  return dispatch => {
    axios.get('/conf')
      .then(res => {
        if (res.data.data.length > 0) {
          let allGonghaoList = res.data.data,
            gonghaoList = allGonghaoList.slice(0, 10);
          const action = {
            type: constants.GET_ALL_GONGHAO_LIST,
            allGonghaoList,
            gonghaoList
          };
          dispatch(action);
        }
      })
  }
};

export const changePage = (page) => {
  return async dispatch => {
    const action = {
      type: constants.CHANGE_PAGE,
      page
    };
    await backTop();
    await dispatch(action);
  }
};

export const changeTab = (key) => {
  return dispatch => {
    const action = {
      type: constants.CHANGE_TAB,
      key
    };
    dispatch(action)
  }
};

const backTop = () => {
  return new Promise((resolve, reject) => {
    let timer;
    timer = setInterval(function () {
      let osTop =
        document.documentElement.scrollTop || document.body.scrollTop;
      let ispeed = Math.floor(-osTop / 1.2);
      document.documentElement.scrollTop = document.body.scrollTop =
        osTop + ispeed;
      if (osTop === 0) {
        clearInterval(timer);
      }
    }, 30);
    resolve(null)
  })
};

export const changeSearchInput = (searchName) => ({
  type: constants.CHANGE_SEARCH_INPUT,
  searchName
});

export const searchByName = (name) => {
  return dispatch => {
    axios.get('/conf/find_one', {params: {name}})
      .then(res => {
        if (res.data.data.length > 0) {
          let allGonghaoList = res.data.data,
            gonghaoList = allGonghaoList.slice(0, 10);
          const action = {
            type: constants.SEARCH_BY_NAME,
            allGonghaoList, gonghaoList
          };
          dispatch(action);
          message.success("查询成功")
        }
      })
  }
};

export const deleteGonghao = (id, index) => {
  return dispatch => {
    Modal.confirm({
      okText: "确定",
      cancelText: "取消",
      content: "确认删除吗？",
title: "提示",
      onOk: () => {
        axios.get("/conf/del", {
          params: {id}
        }).then(res => {
          const action = {
            type: constants.DELETE_GONGHAO,
            index
          };
          dispatch(action);
          message.success("删除成功")
        });
      }
    })
  }
};

