import React, {Component} from 'react';
import {Button, Table} from "antd";
import {connect} from "react-redux";
import {Link} from 'react-router-dom';
import BreadCrumb from '../../../../common/bread';
import {actionCreators} from '../store';
import './index.scss';

class Config extends Component {
  componentWillMount() {
    let code = this.props.match.params.code;
    this.props.getConfigList(code);
  }

  render() {
    const {configList, deleteConfig, history, match} = this.props;
    const configHeader = [
      {
        key: "type",
        title: "类型",
        dataIndex: "type",
        align: "center",
        render: (text, record, index) => {
          return (
            <span>
              {record.type === 0 ? "文本"
                : (record.type === 1 ? "点击"
                  : (record.type === 2 ? "关注"
                    : (record.type === 4 ? "默认回复" : "~~")))}
            </span>
          )
        }
      }, {
        key: "sex",
        title: "性别",
        dataIndex: "sex",
        align: "center",
        render: (text, record, index) => {
          return (
            <span>
              {record.sex === 1 ? "男" : (record.sex === 2
                ? "女" : (record.sex === 0 ? "未知" : "默认"))}
            </span>
          )
        }
      }, {
        key: "text",
        title: "用户回复文本",
        dataIndex: "text",
        align: "center"
      }, {
        key: "key",
        title: "点击关键词",
        dataIndex: "key",
        align: "center"
      }, {
        key: "msgId",
        title: "消息msgId",
        dataIndex: "msgId",
        align: "center"
      }, {
        key: "showUrl",
        title: "图片",
        align: "center",
        render: (text, record, index) => {
          return (
            <img className="table-img" src={record.showUrl} alt="" />
          )
        }
      }, {
        key: "Action",
        title: "Action",
        align: "center",
        width: 120,
        render: (text, record, index) => {
          return (
            <div className="btn-wrapper">
              <Button size="small" type="primary">
                <Link to={{pathname: '/gonghao/tuoguan/config/update/' + match.params.code, query: {record, index}}}>编辑</Link>
              </Button>
              <Button size="small" style={{background: "red", marginLeft: "5px"}} onClick={() => deleteConfig(record._id, index)}>删除</Button>
            </div>
          )
        }
      },
    ];
    return (
      <div>
        <BreadCrumb/>
        <div className="container edit-config">
          <Button icon="double-left" onClick={() => history.goBack()}>
            返回
          </Button>
          <Button type="primary" className="edit-btn">
            <Link to={{pathname: '/gonghao/tuoguan/config/create/' + match.params.code}}>新建配置规则</Link>
          </Button>
          <Table className='config-table' size="small" rowKey={(record => record._id)} columns={configHeader}
                 dataSource={configList} bordered
                 pagination={false}/>
        </div>
      </div>
    )
  }
}

const mapState = state => ({
  configList: state.gonghao.tuoguan.configList
});

const mapDispatch = dispatch => ({
  getConfigList(code) {
    dispatch(actionCreators.getConfigList(code))
  },
  deleteConfig(id, index) {
    dispatch(actionCreators.deleteConfig(id, index))
  }
});

export default connect(mapState, mapDispatch)(Config);