import React, {Component} from 'react';
import {Button, Input, Table, Tabs} from "antd";
import {connect} from "react-redux";
import {Link} from 'react-router-dom';
import BreadCrumb from '../../../common/bread';
import {actionCreators} from './store';
import './index.scss';

const Search = Input.Search;
const {TabPane} = Tabs;

class Tuoguan extends Component {

  componentWillMount() {
    this.props.getGroupList();
    this.props.getAllGonghaoList();
  }

  render() {
    const {gonghaoTagList, gonghaoList, allGonghaoList, changePage, changeTab, currentPage, searchByName, searchName, changeSearchInput, deleteGonghao, jieguan} = this.props;
    const total = allGonghaoList.length;
    const gonghaoHeader = [
      {
        title: 'code',
        dataIndex: 'code',
        key: 'code',
        align: "center"
      }, {
        title: '公号名称',
        dataIndex: 'name',
        key: 'name',
        width: 160,
        align: "center"
      }, {
        title: '分组',
        dataIndex: 'group',
        key: 'group',
        width: 90,
        align: "center",
        render(text) {
          return (<span>{text || "无"}</span>)
        }
      }, {
        title: 'token',
        dataIndex: 'token',
        key: 'token',
        width: 100,
        align: "center"
      }, {
        title: 'EncodingAESKey',
        dataIndex: 'EncodingAESKey',
        key: 'EncodingAESKey',
        align: "center",
        render: (text, record) => (
          <div style={{wordWrap: 'break-word', wordBreak: 'break-all'}}>{text}</div>
        )
      }, {
        title: '链接',
        key: 'url',
        align: "center",
        render: (text, record) => {
          return (
            <div
              style={{wordBreak: 'break-all', minWidth: "100px"}}>{'http://yysd.dmmup.com/weichat/' + record.code}</div>
          )
        }
      }, {
        title: '公号属性',
        key: 'attribute',
        width: 50,
        align: "center",
        render: (text, record, index) => {
          return (
            <span>{record.attribute === 0 ? "默认" : (record.attribute === 1 ? "男" : "女")}</span>
          )
        }
      }, {
        title: '接管状态',
        key: 'status',
        width: 80,
        align: "center",
        render: (text, record, index) => {
          return (
            <span>{record.status === 1 ? "已接管" : (record.status === -2 ? "未接管" : "接管中")}</span>
          )
        }
      }, {
        title: '是否实时更新用户信息',
        key: 'real_time',
        width: 90,
        align: "center",
        render: (text, record, index) => {
          return (
            <span>{record.real_time ? "是" : "否"}</span>
          )
        }
      }, {
        title: 'Action',
        key: 'action',
        width: 245,
        align: "center",
        render: (text, record, index) => {
          return (
            <div className='action-wrapper'>
              <Button size='small' style={{background: "yellowgreen"}}>
                <Link to={{pathname: 'update/', query: record}}>编辑</Link>
              </Button>
              <Button size='small' style={{background: "orange"}}>
                <Link to={'config/index/' + record.code}>配置</Link>
              </Button>
              <Button size='small' style={{background: "red"}}
                      onClick={() => deleteGonghao(record._id, index)}>删除</Button>
              <Button size='small' disabled={record.status !== -2} type='primary'
                      onClick={() => jieguan(record.code)}>接管</Button>
            </div>
          )
        }
      },
    ];
    const tabsRender = gonghaoTagList.map(item => (
      <TabPane tab={item.name} key={item.name}/>
    ));
    const pagination = {
      current: currentPage,
      pageSize: 10,
      defaultCurrent: 1,
      total: total,
      onChange: page => {
        changePage(page)
      }
    };
    return (
      <div>
        <BreadCrumb/>
        <div className='container'>
          <Search
            placeholder='请输入公号名称'
            enterButton="搜索"
            className='search-btn'
            value={searchName}
            onChange={changeSearchInput}
            onSearch={searchName => searchByName(searchName)}
          />
          <div className='fr'>
            <Button type='primary'><Link to='create/'>接管新公号</Link></Button>
            <Button type='dashed' className='reset-code'>重置code(慎用)</Button>
          </div>
          <div className='table-wrapper'>
            <Tabs animated={false} defaultActiveKey="ALL" tabBarGutter={0} onChange={key => changeTab(key)}
                  size='small'>
              <TabPane tab="ALL" key="ALL"/>
              {tabsRender}
            </Tabs>
            <Table className='gonghao-table' size="small" rowKey={(record => record._id)} columns={gonghaoHeader} dataSource={gonghaoList} bordered
                   pagination={pagination}/>
          </div>
        </div>
      </div>
    )
  }
}

const mapState = state => ({
  gonghaoTagList: state.gonghao.tuoguan.gonghaoTagList,
  allGonghaoList: state.gonghao.tuoguan.allGonghaoList,
  gonghaoList: state.gonghao.tuoguan.gonghaoList,
  currentPage: state.gonghao.tuoguan.currentPage,
  searchName: state.gonghao.tuoguan.searchName
});

const mapDispatch = dispatch => ({
  getGroupList() {
    dispatch(actionCreators.getGroupList())
  },
  getAllGonghaoList() {
    dispatch(actionCreators.getAllGonghaoList())
  },
  changePage(page) {
    dispatch(actionCreators.changePage(page))
  },
  changeTab(key) {
    dispatch(actionCreators.changeTab(key))
  },
  changeSearchInput(e) {
    dispatch(actionCreators.changeSearchInput(e.target.value))
  },
  searchByName(value) {
    dispatch(actionCreators.searchByName(value));
  },
  deleteGonghao(id, index) {
    dispatch(actionCreators.deleteGonghao(id, index));
  },
  jieguan(code) {
    dispatch(actionCreators.jieguan(code));
  }
});

export default connect(mapState, mapDispatch)(Tuoguan);