import React, { Component } from 'react';

import { Provider } from 'react-redux';
import { BrowserRouter, Switch, Route, Redirect } from "react-router-dom";

import store from './store';

import HeaderWrapper from './common/header';
import SideWrapper from './common/sider';
import TuoguanRouter from './pages/gonghao/router';

class App extends Component{
  render() {
    const bgStyle = {
      minHeight: "100vh",
      backgroundSize: "100%",
      backgroundRepeat: "no-repeat",
      backgroundAttachment: "fixed",
      backgroundImage: `url(${require("./assets/images/bg.png")})`
    };
    return (
      <Provider store={store}>
        <BrowserRouter>
          <div style={{...bgStyle}}>
            <HeaderWrapper />
            <SideWrapper />
            <div style={{padding: '20px',  marginLeft: '200px'}}>
              <Switch>
                <Route path='/' exact render={() => (<Redirect to='/gonghao' />)} />
                <Route path='/gonghao' component={TuoguanRouter} />
              </Switch>
            </div>
          </div>
        </BrowserRouter>
      </Provider>
    );
  }
}

export default App;
