import React, { Component } from 'react';
import { Link } from "react-router-dom";

import './index.scss';

class HeaderWrapper extends Component {
  render() {
    return (
      <div className='header-wrapper'>
        <Link to="/">
          <h2 className="logo">后台管理</h2>
        </Link>
      </div>
    )
  }
}

export default HeaderWrapper;
