import React, { Component } from 'react';
import BreadCrumb from "../../../../common/bread";
import { connect } from "react-redux";
import { Form, Button, Input, Switch, Radio, Tag } from "antd";
import * as actionCreators from '../store/actionCreators';
import './index.scss';

const resetFields = {
  name: "",
  appid: "",
  appsecret: "",
  token: "mingxingshuo",
  real_time: false,
  save_user: false,
  group: "",
  attribute: 0
};

class EditGonghaoInfo extends Component {

  componentDidMount() {
    let query = this.props.history.location.query;
    if(query) {
      this.props.form.setFieldsValue({...query});
      query.group && this.props.selectTag(query.group)
    } else {
      this.props.form.setFieldsValue({...resetFields});
    }
  }

  render() {
    const { getFieldDecorator, setFieldsValue } = this.props.form;
    const { gonghaoTagList, selectTag, isSelectTag, selectedTag, removeTag, tagConfirm, deleteTagItem, submitForm, history } = this.props;
    const formItemLayout = {
      labelCol: {
        sm: { span: 4 }
      },
      wrapperCol: {
        sm: { span: 8, offset: 0 }
      },
    };
    const tagList = gonghaoTagList.map((item, index) => (
      <Tag closable onClose={() => deleteTagItem(item._id, index)} onClick={() => selectTag(item.name, this.props)} key={item._id}>{item.name}</Tag>
    ));
    let tagOrInput;
    if (isSelectTag) {
      tagOrInput = <Form.Item className='form-item' label="选择标签">
        {getFieldDecorator('group')(<Tag closable onClose={removeTag}>{selectedTag}</Tag>)}
      </Form.Item>;
    } else {
      tagOrInput = <Form.Item className='form-item' label="选择标签">
        {getFieldDecorator('group')(<Input placeholder='请输入标签' onBlur={(e) => tagConfirm(this.props, e)} onKeyDown={(e) => tagConfirm(this.props, e)}/>)}
      </Form.Item>;
    }
    return (
    	<div>
        <BreadCrumb />
        <div className='container edit-gonghao'>
          <h2 className='form-title'>公号接管</h2>
          <Form labelAlign="right" {...formItemLayout}>
            <Form.Item className='form-item' label="公号名称">
              {getFieldDecorator('name', {
                rules: [{ required: true }]
              })(<Input placeholder='请输入公号名称'/>)}
            </Form.Item>
            <Form.Item className='form-item' label="appid">
              {getFieldDecorator('appid', {
                rules: [{ required: true }]
              })(<Input placeholder='请输入appid'/>)}
            </Form.Item>
            <Form.Item className='form-item' label="appsecret">
              {getFieldDecorator('appsecret', {
                rules: [{ required: true }]
              })(<Input placeholder='请输入appsecret'/>)}
            </Form.Item>
            <Form.Item className='form-item' label="token">
              {getFieldDecorator('token', {
                rules: [{ required: true }],
              })(<Input placeholder='请输入token'/>)}
            </Form.Item>
            <Form.Item className='form-item' label="是否实时获取用户信息">
              {getFieldDecorator('real_time', { valuePropName: 'checked' })(<Switch />)}
            </Form.Item>
            <Form.Item className='form-item' label="是否保存用户">
              {getFieldDecorator('save_user', { valuePropName: 'checked' })(<Switch />)}
            </Form.Item>
            {tagOrInput}
            <Form.Item className='form-item' label="标签选项">
              {tagList}
            </Form.Item>
            <Form.Item label="公号属性">
              {getFieldDecorator('attribute')(
                <Radio.Group>
                  <Radio value={0}>默认</Radio>
                  <Radio value={1}>男</Radio>
                  <Radio value={2}>女</Radio>
                </Radio.Group>
              )}
            </Form.Item>
            <Form.Item label=" " colon={false}>
              <Button className="form-btn" type="primary" onClick={() => submitForm(this.props)}>提交</Button>
              <Button onClick={() => {setFieldsValue(resetFields);history.goBack();removeTag()}}>取消</Button>
            </Form.Item>
          </Form>
        </div>
    	</div>
    )
  }
}

const mapState = state =>({
  gonghaoTagList: state.gonghao.tuoguan.gonghaoTagList,
  isSelectTag: state.gonghao.tuoguan.isSelectTag,
  selectedTag: state.gonghao.tuoguan.selectedTag,
  isCreate: state.gonghao.tuoguan.isCreate
});

const mapDispatch = dispatch => ({
  selectTag(name, props){
    dispatch(actionCreators.selectTag(name))
  },
  removeTag() {
    dispatch(actionCreators.removeTag())
  },
  tagConfirm(props, e) {
    let group = e.target.value;
    if(e.keyCode && e.keyCode === 13 && group) {
      dispatch(actionCreators.tagConfirm(group, props));
      return;
    }
    if(!e.keyCode && group) {
      dispatch(actionCreators.tagConfirm(group, props))
    }
  },
  deleteTagItem(id, index) {
    dispatch(actionCreators.deleteTagItem(id, index))
  },
  submitForm(props) {
    let gonghaoInfo = props.form.getFieldsValue();
    gonghaoInfo.group = props.selectedTag;
    // if(props.history.location.query) {
    //   gonghaoInfo._id = props.history.location.query._id;
    //   dispatch(actionCreators.updateForm(gonghaoInfo, props.history))
    // } else {
    //   dispatch(actionCreators.createForm(gonghaoInfo, props.history))
    // }
    props.form.setFieldsValue(resetFields);
    dispatch(actionCreators.removeTag())
  }
});

export default connect(mapState, mapDispatch)(Form.create({ name: 'editGonghao' })(EditGonghaoInfo));