import React, {Component} from 'react';
import {Menu, Icon} from 'antd';
import {Link} from 'react-router-dom';

import './index.scss';

const {SubMenu} = Menu;
const MenuItem = Menu.Item;

class SideWrapper extends Component {
  menuList = [
    {
      keyIndex: 'gonghao',
      title: '公号管理',
      icon: 'appstore',
      subMenuList: [
        {
          keyIndex: 'tuoguan',
          title: '公号托管',
          path: '/gonghao/tuoguan'
        }, {
          keyIndex: 'menu',
          title: '配置菜单',
          path: '/gonghao/menu'
        }, {
          keyIndex: 'material',
          title: '素材管理',
          path: '/gonghao/material'
        }, {
          keyIndex: 'timingMenu',
          title: '定时菜单',
          path: '/gonghao/timingMenu'
        }
      ]
    }, {
      keyIndex: 'kefu',
      title: '客服消息',
      icon: 'snippets',
      subMenuList: [
        {
          keyIndex: 'kMessage',
          title: '消息管理',
          path: '/kefu/kMessage'
        }
      ]
    }, {
      keyIndex: 'group',
      title: '群发消息',
      icon: 'dashboard',
      subMenuList: [
        {
          keyIndex: 'gMessage',
          title: '消息管理',
          path: '/group/gMessage'
        }
      ]
    }, {
      keyIndex: 'novel',
      title: '小说链接',
      icon: 'switcher',
      subMenuList: [
        {
          keyIndex: 'links',
          title: '小说链接列表',
          path: '/novel/links'
        }, {
          keyIndex: 'spread',
          title: '推广链接',
          path: '/novel/spread'
        }, {
          keyIndex: 'adMaterial',
          title: '广告素材',
          path: '/novel/adMaterial'
        }, {
          keyIndex: 'channel',
          title: '渠道管理',
          path: '/novel/channel'
        }, {
          keyIndex: 'tools',
          title: '小说工具',
          path: '/novel/tools'
        }, {
          keyIndex: 'novelSpread',
          title: '小说推广链接',
          path: '/novel/novelSpread'
        }, {
          keyIndex: 'backNovel',
          title: '返回小说推荐',
          path: '/novel/backNovel'
        }
      ]
    }, {
      keyIndex: 'wechat',
      title: '微信小工具',
      icon: 'tool',
      subMenuList: [
        {
          keyIndex: 'qrCode',
          title: '参数二维码',
          path: '/wechat/qrCode'
        }, {
          keyIndex: 'tag',
          title: '标签管理',
          path: '/wechat/tag'
        }, {
          keyIndex: 'qiangguan',
          title: '强关链接',
          path: '/wechat/qiangguan'
        }
      ]
    }
  ];

  state = {
    selectedKeys: [window.location.pathname.split('/')[2] || 'tuoguan']
  };

  onSelect = async (selectItem) => {
    const latestSelectedKey = selectItem.selectedKeys.find(key => this.state.selectedKeys.indexOf(key) === -1);
    let selectList = [];
    await this.menuList.map(item => (selectList = [...selectList, ...item.subMenuList]));
    await selectList.find(item => (
      item.keyIndex === latestSelectedKey
      && this.setState({ selectedKeys: latestSelectedKey ? [latestSelectedKey] : ['tuoguan'] })
    ))
  };

  render() {
    const pathName = window.location.pathname;
    const MenuListRender = this.menuList.map((item, index) => {
      const SubMenuList = item.subMenuList.map(subItem => (
          <MenuItem key={subItem.keyIndex}><Link to={subItem.path}>{subItem.title}</Link></MenuItem>
      ));
      return (<SubMenu
        key={item.keyIndex}
        title={
          <span className='side-item'>
            <Icon className="side-icon" type={item.icon}/>
            <span>{item.title}</span>
          </span>
        }
      >
        {SubMenuList}
      </SubMenu>)
    });
    return (
      <div className='side-wrapper'>
        <Menu
          mode="inline"
          theme="dark"
          selectedKeys={this.state.selectedKeys}
          defaultOpenKeys={[window.location.pathname.split('/')[1] || 'gonghao']}
          onSelect={this.onSelect}
          defaultSelectedKeys={[pathName]}
        >
          {MenuListRender}
        </Menu>
      </div>
    )
  }
}

export default SideWrapper;
