import React, { Component } from 'react';
import { Breadcrumb } from 'antd';

import './index.scss';

const breadList = {
  '/gonghao': '公号管理',
  '/gonghao/tuoguan': '公号托管',
  '/gonghao/tuoguan/create': '接管公号',
  '/gonghao/tuoguan/update': '修改公号',
  '/gonghao/tuoguan/config': '公号配置',
  '/gonghao/tuoguan/config/create': '新建配置',
  '/gonghao/tuoguan/config/update': '修改配置',
  '/gonghao/menu': '配置菜单',
  '/gonghao/material': '素材管理',
  '/gonghao/timingMenu': '定时菜单',
  '/kefu': '客服消息',
  '/kefu/kMessage': '消息管理',
  '/group': '群发消息',
  '/group/gMessage': '消息管理',
  '/novel': '小说链接',
  '/novel/links': '小说链接列表',
  '/novel/spread': '推广链接',
  '/novel/adMaterial': '广告素材',
  '/novel/channel': '渠道管理',
  '/novel/tools': '小说工具',
  '/novel/novelSpread': '小说推广链接',
  '/novel/backNovel': '返回小说推荐',
  '/wechat': '微信小工具',
  '/wechat/qrCode': '参数二维码',
  '/wechat/tag': '标签管理',
  '/wechat/qiangguan': '强关链接'
};

class BreadWrapper extends Component {
  constructor(props) {
    super(props);
    this.state = {
      pathSnippets: null,
      extraBreadcrumbItems: null,
    }
  }

  getPath = () => {
    this.setState({
      pathSnippets: window.location.pathname.split('/').slice(1)
    });
    this.setState((prev) => {
      return {
        extraBreadcrumbItems: prev.pathSnippets.map((_, index) => {
          let url = `/${prev.pathSnippets.slice(0, index + 1).join('/')}`;
          return (
            <Breadcrumb.Item key={url}>
              {breadList[url]}
            </Breadcrumb.Item>
          );
        })
      }
    })
  };

  componentWillMount() {
    this.getPath()
  }

  render() {
    return (
    	<div className='bread-wrapper'>
    		<div className='bread-nav'>
          <Breadcrumb>
            {this.state.extraBreadcrumbItems}
          </Breadcrumb>
        </div>
    	</div>
    )
  }

}

export default BreadWrapper;