import React, { Component } from 'react';
import BreadCrumb from "../../../../../common/bread";
import { connect } from "react-redux";
import { Form, Button, Input, Upload, Radio, Icon } from "antd";
import * as actionCreators from '../../store/actionCreators';

const resetFields = {
  type: 0,
  sex: 3,
  attribute: 1,
  text: "",
  replyType: 0,
  msgId: null,
  key: "",
  urlList: []
};

let showUrl = "", url = "";

class EditGonghaoInfo extends Component {

  componentWillMount() {
    let query = this.props.history.location.query;
    if(query) {
      this.props.changeConfigForm(query.record)
      query.record.showUrl && (showUrl = query.record.showUrl);
      query.record.url && (url = query.record.url);
    }
  }

  render() {
    const { getFieldDecorator } = this.props.form;
    const { submitForm, history, configForm } = this.props;
    const formItemLayout = {
      labelCol: {
        sm: { span: 4 }
      },
      wrapperCol: {
        sm: { span: 8, offset: 0 }
      },
    };
    let UploadImg, TextOrKey, TextOrImg;
    if(showUrl) {
      UploadImg = <img style={{width: "100px"}} src={showUrl} alt="" />
    } else {
      UploadImg = <Button><Icon type="upload" /> 上传图片 </Button>
    }
    if(configForm.type === 0) {
      TextOrKey =<Form.Item label="用户回复内容">
        {getFieldDecorator('text', {
          rules: [{ required: true }],
          initialValue: configForm.text || ""
        })(<Input placeholder='请输入用户回复内容'/>)}
      </Form.Item>
    } else if(configForm.type === 1) {
      TextOrKey = <Form.Item label="菜单栏点击关键词">
        {getFieldDecorator('key', {
          rules: [{ required: true }],
          initialValue: configForm.key || ""
        })(<Input placeholder='请输入菜单栏点击关键词'/>)}
      </Form.Item>
    }
    if(configForm.replyType === 0) {
      TextOrImg = <Form.Item label="素材msgId">
          {getFieldDecorator('msgId', {
            rules: [{ required: true }],
            initialValue: configForm.msgId || ""
          })(<Input placeholder='请输入素材msgId'/>)}
        </Form.Item>
    } else if(configForm.replyType === 1) {
      TextOrImg = <Form.Item label="图片">
        {getFieldDecorator('urlList', {
          rules: [{ required: true }],
          initialValue: []
        })(
          <Upload name="imageFile" action="/reply/upload" onChange={this.uploadImg.bind(this)} showUploadList={false}>
            {UploadImg}
          </Upload>
        )}
      </Form.Item>
    }
    return (
    	<div>
        <BreadCrumb />
        <div className='container edit-gonghao'>
          <h2 className='form-title'>公号配置</h2>
          <Form labelAlign="right" {...formItemLayout}>
            <Form.Item label="公号属性">
              {getFieldDecorator('type', {
                rules: [{ required: true }],
                initialValue: configForm.type || 0
              })(
                <Radio.Group onChange={this.changeType.bind(this)}>
                  <Radio.Button value={0}>文本</Radio.Button>
                  <Radio.Button value={1}>点击</Radio.Button>
                  <Radio.Button value={2}>关注</Radio.Button>
                  <Radio.Button value={4}>默认回复</Radio.Button>
                </Radio.Group>
              )}
            </Form.Item>
            <Form.Item label="性别">
              {getFieldDecorator('sex', {
                initialValue: configForm.sex || 3
              })(
                <Radio.Group>
                  <Radio value={3}>默认</Radio>
                  <Radio value={1}>男</Radio>
                  <Radio value={2}>女</Radio>
                  <Radio value={0}>未知</Radio>
                </Radio.Group>
              )}
            </Form.Item>
            <Form.Item label="未知性别">
              {getFieldDecorator('attribute', {
                initialValue: configForm.attribute || 1
              })(
                <Radio.Group>
                  <Radio value={1}>男</Radio>
                  <Radio value={2}>女</Radio>
                </Radio.Group>
              )}
            </Form.Item>
            {TextOrKey}
            <Form.Item label="回复内容类型">
              {getFieldDecorator('replyType', {
                rules: [{ required: true }],
                initialValue: configForm.replyType || 0
              })(
                <Radio.Group onChange={this.changeReplyType.bind(this)}>
                  <Radio.Button value={0}>文本/图文</Radio.Button>
                  <Radio.Button value={1}>图片</Radio.Button>
                </Radio.Group>
              )}
            </Form.Item>
            {TextOrImg}
            <Form.Item label=" " colon={false}>
              <Button className="form-btn" type="primary" onClick={() => submitForm(this.props)}>提交</Button>
              <Button onClick={() => {history.goBack();}}>取消</Button>
            </Form.Item>
          </Form>
        </div>
    	</div>
    )
  }
  uploadImg(info) {
    if (info.file.status === 'done') {
      showUrl = `http://test.oorggt.top/uploads/${info.file.response.filename}`
      url = `./public/uploads/${info.file.response.filename}`
    }
  }
  changeType(e) {
    let resetForm = this.props.form.getFieldsValue();
    resetForm.type = e.target.value;
    this.props.changeConfigForm({...resetForm})
  }
  changeReplyType(e) {
    let resetForm = this.props.form.getFieldsValue();
    resetForm.replyType = e.target.value;
    showUrl = url = "";
    this.props.changeConfigForm({...resetForm})
  }
  componentWillUnmount() {
    showUrl = url = "";
    this.props.changeConfigForm({...resetFields})
  }
}

const mapState = state =>({
  configForm: state.gonghao.tuoguan.configForm
});

const mapDispatch = dispatch => ({
  changeConfigForm(formInfo) {
    dispatch(actionCreators.changeConfigForm(formInfo))
  },
  submitForm(props) {
    let configInfo = props.form.getFieldsValue(), query = props.history.location.query;
    Object.assign(configInfo, {showUrl, url});
    configInfo.code = parseInt(props.match.params.code);
    if(query) {
      configInfo.id = query.record._id;
      dispatch(actionCreators.updateConfigForm(configInfo, props.history, query.index))
    } else {
      dispatch(actionCreators.createConfigForm(configInfo, props.history))
    }
  }
});

export default connect(mapState, mapDispatch)(Form.create({ name: 'editGonghao' })(EditGonghaoInfo));