import React, { Component } from 'react';
import { Switch, Route, Redirect } from 'react-router-dom';
import Tuoguan from './tuoguan';
import Menu from './menu';
import editGonghao from './tuoguan/edit';
import gonghaoConfig from './tuoguan/config';
import editConfig from './tuoguan/config/edit';

class TuoguanWrapper extends Component {
  render() {
    return (
    	<Switch>
        <Route path='/gonghao/tuoguan/index' component={Tuoguan} />
        <Route path='/gonghao/tuoguan/create' component={editGonghao} />
        <Route path='/gonghao/tuoguan/update' component={editGonghao} />
        <Route path='/gonghao/tuoguan/config/index/:code' component={gonghaoConfig} />
        <Route path='/gonghao/tuoguan/config/create/:code' component={editConfig} />
        <Route path='/gonghao/tuoguan/config/update/:code' component={editConfig} />
        <Route path='/gonghao/menu' component={Menu} />
        <Redirect from='/gonghao' exact to='/gonghao/tuoguan/index' />
        <Redirect from='/gonghao/tuoguan' exact to='/gonghao/tuoguan/index' />
        <Redirect from='/gonghao/tuoguan/config' exact to='/gonghao/tuoguan/config/index' />
    	</Switch>
    )
  }
}

export default TuoguanWrapper;